import { useMemo } from 'react';
import {
  // animated
  AnimatedBarGroup,
  AnimatedBarStack,

  // default
  XYChart,
  Grid,
  Axis,
  BarGroup,
  BarStack,
  BarSeries,
  Tooltip
} from '@visx/xychart';
import dayjs from 'dayjs';

type ValueKey = 'value1' | 'value2' | 'value3';

export type Item = {
  date: string;
  value1: string;
  value2?: string;
  value3?: string;
};

export type BarChartProps = {
  variant?: 'group' | 'stack';
  width?: number;
  height?: number;
  showGridRows?: boolean;
  showGridColumns?: boolean;
  label?: string;
  data: Item[];
  chartOrientation?: 'horizontal' | 'vertical';
  xAxisOrientation?: 'top' | 'bottom';
  yAxisOrientation?: 'left' | 'right';
  numTicks?: number;
  showTooltip?: boolean;
  useAnimated?: boolean;
};

const getDate = (d: Item) => dayjs(d.date).format('YY MM');
const getValue1Temperature = (d: Item) => Number(d['value1']);
const getValue2Temperature = (d: Item) => Number(d['value2']);
const getValue3Temperature = (d: Item) => Number(d['value3']);

// TODO: Add Props. 1. axis fontSize. 2. axis hasTicks, 3. data keyName customizing, barSeries colorAccessor Array

const Bar = ({
  variant = 'group',
  width = 400,
  height = 400,
  showGridRows = true,
  showGridColumns = false,
  label,
  data,
  chartOrientation = 'vertical',
  xAxisOrientation = 'bottom',
  yAxisOrientation = 'left',
  numTicks = 5,
  showTooltip = true,
  useAnimated = true
}: BarChartProps) => {
  const accessors = useMemo(
    () => ({
      x: {
        value1: chartOrientation === 'vertical' ? getDate : getValue1Temperature,
        value2: chartOrientation === 'vertical' ? getDate : getValue2Temperature,
        value3: chartOrientation === 'vertical' ? getDate : getValue3Temperature
      },
      y: {
        value1: chartOrientation === 'vertical' ? getValue1Temperature : getDate,
        value2: chartOrientation === 'vertical' ? getValue2Temperature : getDate,
        value3: chartOrientation === 'vertical' ? getValue3Temperature : getDate
      },
      date: getDate
    }),
    [chartOrientation]
  );

  const ChartType =
    variant === 'group'
      ? useAnimated
        ? AnimatedBarGroup
        : BarGroup
      : useAnimated
      ? AnimatedBarStack
      : BarStack;

  return (
    <div className="graph-container">
      <XYChart
        width={width}
        height={height}
        yScale={{
          type: chartOrientation === 'vertical' ? 'linear' : 'band',
          padding: chartOrientation === 'vertical' ? 0 : variant === 'stack' ? 0.5 : 0.2
        }}
        xScale={{
          type: chartOrientation === 'vertical' ? 'band' : 'linear',
          padding: chartOrientation === 'vertical' ? (variant === 'stack' ? 0.5 : 0.2) : 0
        }}
      >
        {(showGridRows || showGridColumns) && (
          <Grid
            key="grid"
            rows={showGridRows}
            columns={showGridColumns}
            numTicks={numTicks}
            className="opacity-30"
          />
        )}
        <Axis
          orientation={chartOrientation === 'horizontal' ? yAxisOrientation : xAxisOrientation}
        />
        <Axis
          key={`temp-axis-${chartOrientation}`}
          label={label}
          orientation={chartOrientation === 'horizontal' ? xAxisOrientation : yAxisOrientation}
          numTicks={numTicks}
        />
        <ChartType>
          <BarSeries
            dataKey="value1"
            data={data}
            xAccessor={accessors.x['value1']}
            yAccessor={accessors.y['value1']}
            colorAccessor={() => '#85ceff'}
            radiusTop={chartOrientation === 'vertical' && variant === 'group'}
            radiusRight={chartOrientation === 'horizontal' && variant === 'group'}
            radius={variant === 'group' ? 4 : 0}
          />
          <BarSeries
            dataKey="value2"
            data={data}
            xAccessor={accessors.x['value2']}
            yAccessor={accessors.y['value2']}
            colorAccessor={() => '#009aff'}
            radiusTop={chartOrientation === 'vertical' && variant === 'group'}
            radiusRight={chartOrientation === 'horizontal' && variant === 'group'}
            radius={variant === 'group' ? 4 : 0}
          />
          <BarSeries
            dataKey="value3"
            data={data}
            xAccessor={accessors.x['value3']}
            yAccessor={accessors.y['value3']}
            colorAccessor={() => '#00568f'}
            radiusTop={chartOrientation === 'vertical'}
            radiusRight={chartOrientation === 'horizontal'}
            radius={4}
          />
        </ChartType>

        {showTooltip && (
          <Tooltip<Item>
            snapTooltipToDatumY
            renderTooltip={({ tooltipData, colorScale }) => (
              <>
                {/** date */}
                {(tooltipData?.nearestDatum?.datum &&
                  accessors.date(tooltipData?.nearestDatum?.datum)) ||
                  'No date'}
                <br />
                <br />
                {/** temperatures */}
                {[tooltipData?.nearestDatum?.key]
                  .filter((valueName) => valueName)
                  .map((valueName) => {
                    const temperature =
                      tooltipData?.nearestDatum?.datum &&
                      accessors.y[valueName as ValueKey](tooltipData?.nearestDatum?.datum);
                    return (
                      <div key={valueName}>
                        <em
                          style={{
                            color: colorScale?.(valueName as ValueKey),
                            textDecoration:
                              tooltipData?.nearestDatum?.key === valueName ? 'underline' : undefined
                          }}
                        >
                          {valueName}
                        </em>{' '}
                        {temperature == null || Number.isNaN(temperature) ? '' : temperature}
                      </div>
                    );
                  })}
              </>
            )}
          />
        )}
      </XYChart>
    </div>
  );
};

export default Bar;
